.container {
  background-color: #fff;
  padding: 20px;
  margin: 15px 0;
  border-radius: 10px;
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.08); }
  .container .containerMedia {
    max-width: 200px; }
    .container .containerMedia img {
      width: 100%;
      box-shadow: 0 2px 16px 0 #00000033;
      margin-bottom: 20px; }
  .container .containerDetails {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
    .container .containerDetails .imageWrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 2px; }
      .container .containerDetails .imageWrapper .proImage {
        width: 30px;
        height: 30px;
        overflow: hidden;
        border-radius: 50%;
        box-shadow: 0 2px 16px 0 #00000033; }
    .container .containerDetails .detailWrapper {
      width: 93%; }
      .container .containerDetails .detailWrapper a {
        text-decoration: none; }
      .container .containerDetails .detailWrapper .tags {
        display: flex;
        justify-content: flex-start;
        align-items: center; }
        .container .containerDetails .detailWrapper .tags .tag {
          max-width: 100px; }
      .container .containerDetails .detailWrapper h1 {
        margin-block-start: 0;
        font-family: "Montserrat regular";
        letter-spacing: 1px;
        font-weight: 100;
        font-size: 25px;
        color: #000;
        cursor: pointer; }
      .container .containerDetails .detailWrapper p {
        font-family: "Lora regular";
        color: #636e72;
        line-height: 1.5;
        letter-spacing: 1px;
        font-size: 14px;
        cursor: pointer; }
      .container .containerDetails .detailWrapper .iconArea {
        display: flex;
        justify-content: flex-start;
        margin-top: 30px; }
        .container .containerDetails .detailWrapper .iconArea .icon {
          display: flex;
          align-items: center;
          width: 40px;
          justify-content: flex-start;
          margin-right: 20px;
          cursor: pointer; }
          .container .containerDetails .detailWrapper .iconArea .icon .amount {
            margin-left: 5px;
            font-size: 10px;
            color: #636e72;
            font-family: "Montserrat regular"; }
