.space {
  height: 60px; }

.container {
  height: 20px;
  background-color: #fff;
  z-index: 4;
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.08);
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 20px 0; }
  .container .wrapper {
    display: flex;
    width: 70%;
    align-items: center;
    justify-content: space-between; }
    .container .wrapper .headerLeft {
      padding: 20px; }
      .container .wrapper .headerLeft .siteName {
        font-size: 18px;
        font-family: "Montserrat regular";
        margin: 0;
        color: #696969;
        font-weight: 100; }
      .container .wrapper .headerLeft .logo {
        width: 25px;
        height: 25px; }
      .container .wrapper .headerLeft a {
        text-decoration: none;
        color: #00bcd4;
        display: flex;
        align-items: center; }
      .container .wrapper .headerLeft .tagline {
        font-size: 12px;
        letter-spacing: 1px;
        margin: 0;
        color: #000; }
    .container .wrapper .headerRight {
      padding: 20px; }
      .container .wrapper .headerRight a {
        text-decoration: none; }
