.container {
  padding: 2px 10px;
  background-color: #ED4C67;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  margin: 5px;
  font-family: "Montserrat regular";
  font-size: 10px; }

.selected {
  background-color: #ff4757;
  animation: active 0.3s ease-in; }

.unselected {
  background-color: #ced6e0;
  color: #000; }

@keyframes active {
  0% {
    opacity: 0.5;
    background-color: #ced6e0; }
  100% {
    opacity: 1;
    background-color: #ff4757; } }
