.container .wrapper {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center; }
  .container .wrapper .createFormOuter {
    margin: 40px 0 10px 0;
    width: 60%;
    max-width: 1000px; }
    .container .wrapper .createFormOuter .secondForm {
      width: 100%;
      background-color: #f1f2f6;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 14px 0 0 0;
      padding: 20px 0;
      animation: appear .1s ease-out;
      animation: activate .2s ease-out; }
      .container .wrapper .createFormOuter .secondForm .actionsArea {
        display: flex;
        align-items: flex-start;
        justify-content: space-between; }
      .container .wrapper .createFormOuter .secondForm textarea {
        animation: appear .1s ease-out;
        height: 60px;
        width: 95%;
        border-radius: 10px;
        border: none;
        padding: 10px;
        font-size: 15px;
        font-family: "Montserrat regular";
        resize: none;
        overflow: hidden; }
        .container .wrapper .createFormOuter .secondForm textarea:focus {
          outline: none; }
    .container .wrapper .createFormOuter .optionsBar {
      width: 90%;
      margin: 10px 5px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      font-size: 10px;
      font-family: "Montserrat light";
      color: #696969; }
      .container .wrapper .createFormOuter .optionsBar .feedImage {
        width: 200px;
        animation: activate .6s ease-out; }
        .container .wrapper .createFormOuter .optionsBar .feedImage img {
          width: 100%; }
      .container .wrapper .createFormOuter .optionsBar .optionsBarTags {
        animation: activate .8s ease-in-out;
