        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap; }
    .container .wrapper .createFormOuter .createFeedConainer {
      background-color: #f1f2f6;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .container .wrapper .createFormOuter .createFeedConainer .buttonArea {
        width: 20%;
        display: flex;
        justify-content: center;
        align-items: center; }
        .container .wrapper .createFormOuter .createFeedConainer .buttonArea .buttonCreate {
          animation: activate .6s ease-out;
          background-color: #ED4C67;
          color: #fff;
          font-weight: 800;
          font-size: 12px;
          padding: 10px;
          border-radius: 2px;
          font-family: "Montserrat regular";
          letter-spacing: 1px;
          cursor: pointer; }
        .container .wrapper .createFormOuter .createFeedConainer .buttonArea .buttonCreateDis {
          color: #636e72;
          font-weight: 800;
          font-size: 12px;
          padding: 10px;
          border-radius: 2px;
          font-family: "Montserrat regular";
          letter-spacing: 1px; }
      .container .wrapper .createFormOuter .createFeedConainer .textAreaWrapper {
        border-radius: 10px;
        padding: 10px;
        width: 80%;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between; }
        .container .wrapper .createFormOuter .createFeedConainer .textAreaWrapper textarea {
          width: 90%;
          border: none;
          font-size: 15px;
          font-family: "Montserrat regular";
          resize: none;
          overflow: hidden; }
          .container .wrapper .createFormOuter .createFeedConainer .textAreaWrapper textarea:focus {
            outline: none; }
  .container .wrapper .wrapperInner {
    max-width: 1000px;
    width: 60%; }

.maxCharactorWarning {
  color: #e84118;
  animation: activate .8s linear;
  margin: 8px 25px;
  font-size: 10px;
  font-weight: 800;
  font-family: "Montserrat light"; }

.boxBorder {
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.08); }

.option {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: pointer; }
  .option input {
    width: 40px;
    opacity: 0;
    cursor: pointer; }

.loginContainer {
  width: 300px;
  height: 250px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  border-radius: 10px; }
  .loginContainer .closeBtn {
    color: #000;
    top: 5px;
    left: 5px;
    z-index: 6;
    cursor: pointer; }
  .loginContainer h3 {
    font-family: "Montserrat regular"; }

@keyframes activate {
  0% {
    opacity: 0; }
  100% {
    transform: 1; } }

@keyframes appear {
  0% {
    height: 0; }
  100% {
    height: 40px; } }
