.container {
  margin: 20px 0 10px 0;
  animation: active 0.6s ease-in; }

.commentView {
  border-radius: 5px;
  padding: 10px 0 0 10px;
  margin: 10px 0; }
  .commentView .replyBtn {
    font-size: 12px;
    font-family: "Montserrat light";
    cursor: pointer; }
  .commentView .commentAuther {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
    .commentView .commentAuther .commentAutherImg {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden; }
    .commentView .commentAuther .commentName {
      font-size: 12px;
      font-family: "Montserrat light";
      margin-left: 10px; }
  .commentView p {
    font-family: "Montserrat regular"; }

.activeClass {
  animation: active 0.3s ease-in; }

.writeComment {
  margin: 40px 0;
  width: 67%;
  background-color: #f1f2f6;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .writeComment .replyBtn {
    animation: activate .6s ease-out;
    background-color: #ED4C67;
    color: #fff;
    font-weight: 800;
    font-size: 12px;
    padding: 10px;
    border-radius: 2px;
    font-family: "Montserrat regular";
    letter-spacing: 1px;
    cursor: pointer;
    margin-left: 20px; }
  .writeComment .textAreaWrapper {
    border-radius: 10px;
    padding: 5px 10px;
    width: 79%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .writeComment .textAreaWrapper .optionsArea {
      width: 4%;
      display: flex;
      align-items: center; }
      .writeComment .textAreaWrapper .optionsArea input {
        position: relative;
        top: -5px;
        left: -48px;
        right: 54px;
        width: 33px;
        opacity: 0;
        width: 2%; }
    .writeComment .textAreaWrapper textarea {
      width: 90%;
      border: none;
      font-size: 12px;
      font-family: "Montserrat regular";
      resize: none;
      overflow: hidden; }
      .writeComment .textAreaWrapper textarea:focus {
        outline: none; }

.commentImage {
  width: 200px; }
  .commentImage img {
    width: 100%; }

@keyframes active {
  0% {
    opacity: 0.5; }
  100% {
    opacity: 1; } }
